import Vue from 'vue';
import Router from 'vue-router';
import Livestream from '@/views/livestream-native-janus.vue';
import Livestream2 from '@/views/livestream-native-mu38.vue';
import Livestream3 from '@/views/livestream-native-kurento.vue';
import Livestream4 from '@/views/livestream-native.vue';


Vue.use(Router);

const router = new Router({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{   path: '/', component: Livestream },
		{   path: '/290p', component: Livestream2 },
		{   path: '/390p', component: Livestream3 },
		{   path: '/490p', component: Livestream4 }
      ]
});

export default router;