













































































































































































































































































	import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	import db from "@/firebase";
	//import shortid from "shortid";
	
    declare global {
		interface Window { 
			PrismicJS: any, videojs: any, tns: any, PrismicDOM: any, Janus: any  
		}	
	}

	var touchEvent = 'onclick' in window ? 'click' : 'touchstart';

	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class LiveStream extends Vue {
		_socketServer = 'wss://07b7db0c469d.ngrok.io';
		ws = new WebSocket(this._socketServer, 'janus-protocol');

		prismicDOM = window.PrismicDOM; 
		viewProgram: boolean = false;
		remoteVideo: HTMLVideoElement;
		askQuestionModal: boolean = false;
		question: any = {};
		currentMedia: any = {title:'', date:''};
		isPlaying: boolean = false;
		chatRegistered = false;
	
        offerOptions: any ={ offerToReceiveVideo: 1, offerToReceiveAudio: 1 };
        servers:any = {
            'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}, 
            {'urls': 'turn:numb.viagenie.ca','credential': 'beaver','username': 'webrtc.websitebeaver@gmail.com'}]
		};
		

		programs: any = {};
		get hasProgram() {
			return Object.keys(this.programs).length == 0;
		}

		audioVolume: number = 0.2;
		connections:any = {};
		player:any = null;
		player0: HTMLMediaElement;
		paused:boolean = false;
		streamEnd:boolean = true; // false
		stream: MediaStream;

		isProgram: boolean = true;

		newStyle: any = [];
        modalEnd: any = null;
        modalPause: any = null;
		verse: any = {};
		showVerse = false;
		banner: any = {};
		showBanner = false;
        service_section = 'chat';
        chatList: string[] = [];
		chat: any = {name:'', content:''};
		isMuted: boolean = false;
		currentTime = '';
		intervalId = null;

		sfuHandle = null; textHandle = null; janus = null;

		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => vm._getPage());
		}

        @Watch('streamEnd') _toggleVideoModal(){
            if(this.streamEnd == true) this.modalEnd.open();
            else{this.modalEnd.close();}
        }

		public mounted() {
			db.VIDEO_STARTED_SIGNAL.on("value",this.streamStarted);

			document.getElementById('chatinput').addEventListener('keyup', (e) => {
				if (e.keyCode === 13) this.sendChat();
			});
			
			this.player = window.videojs('video-id', {
				//liveui: true,
				responsive:true, controlBar: {
					pictureInPictureToggle: false,
					durationDisplay: false, timeDivider: false, liveDisplay: false,
					fullscreenToggle: false, progressControl: false
				}
			});

			this.remoteVideo = this.player.tech().el();
			this.player.ready(() => {
				this.player.controlBar.playToggle.on('click', (e) => {
					this.isPlaying? this._pause() : this._play();
				});
			});

            var ModalDialog = window.videojs.getComponent('ModalDialog');
            this.modalEnd = new ModalDialog(this.player, {temporary: false, uncloseable: true});
            this.modalEnd.addClass('not-started-modal');
            this.player.addChild(this.modalEnd);
			
			this.modalPause = new ModalDialog(this.player, {temporary: false, uncloseable: true});
            this.modalPause.addClass('pause-modal');
            this.player.addChild(this.modalPause);
			
			this.modalEnd.open();

			window.onbeforeunload = () => {
				//var off = db.DISCONNECTED.push(JSON.stringify({uuid: this.uuid}));
				//off.remove();
				if(this.intervalId != null) clearInterval(this.intervalId);
			}
		}

		async _getPage() {
			this._getPrograms();
		}

		async _getPrograms() {
			var response = (await _Prismic()).getSingle('program');
			response.then(d => this.programs = d.data.programs);
		}

		_sendQuestion() {
			if(this.streamEnd) return;
			if(!this.chatRegistered) this.registerChat(this.question.name || 'anonymous');

			let message = {
				textroom: 'message', to: 'pastor',
				transaction: this.randomString(12), room: 7305, text: this.question.content,
			}
			this.textHandle.data({text: JSON.stringify(message)})
            this.question = {};
			this.askQuestionModal = false;
		}

		beforeDestroy() {
			if(this.player) this.player.dispose();
		}

        sendChat() {
			if(this.chat.content == '') return;
			let message = {
				textroom: 'message', transaction: this.randomString(12), room: 7305, text: this.chat.content
			}
			this.textHandle.data({text: JSON.stringify(message)})
            this.chat={...this.chat, content:''};
		}
		
		toggleFullScreen() {
			this.player.isFullscreen()? this.player.exitFullscreen(): this.player.requestFullscreen();
		}

		_play() {
			this.remoteVideo.play();
			this.isPlaying = true;
		}

		_pause() {
			this.remoteVideo.pause(); 
            this.isPlaying = false;
		}

		_mute() {
			this.remoteVideo.muted = true;
			this.isMuted = true;
			//this.isPlaying = true;// !this.player.paused;
		}

		_unmute() {
			this.remoteVideo.muted = false; 
			this.isMuted = false;
			//this.isPlaying = false;//this.player.paused;
		}

		registerChat(name) {
			if(!this.chatRegistered){
				this.janus.attach({
					plugin: 'janus.plugin.textroom',
					success: (handle) =>{
						this.textHandle = handle;
						this.textHandle.send({message: {request: 'setup'}});
					},

					onmessage: (message, desc) =>{
						if(desc) this.textHandle.createAnswer({
							jsep: desc, media: {audio: false, video: false, data: true },
							success: (jsep) => this.textHandle.send({ message:{ request: 'ack'}, jsep }),
							error: (error) => {}
						})
					},
					ondata: (data) => {
						data = JSON.parse(data);
						if(data['textroom'] == 'message') this.chatList=[...this.chatList, data]
					},
					ondataopen: () => {
						let register = {textroom: 'join', room: 7305, username: name, display: name, transaction: this.randomString(12)};
						this.textHandle.data({text: JSON.stringify(register)});
					}
				});
				this.chat = { ...this.chat, name};
				this.chatRegistered = true;
			}
		}

		registerStream() {
			this.janus.attach({
				plugin: 'janus.plugin.videoroom',
				success: (handle) =>{
					this.sfuHandle = handle;
					var subscriber = { request: 'join', room: 1, ptype: 'subscriber', feed: 27052 };
					this.sfuHandle.send({message: subscriber});
				},

				onmessage: (message, desc) =>{
					if(desc) this.sfuHandle.createAnswer({
						jsep: desc, media: {audioSend: false, videoSend: false, data: true },
						success: (jsep) => this.sfuHandle.send({ message:{ request: 'start', room: 1}, jsep }),
						error: (error) => {}
					})
				},
				ondata: (d) => {
					if(this.streamEnd) return;
					let data = JSON.parse(d);
					if(data['verse'])
						if(data['show'] == true) {
							this.verse = data; this.showVerse = true;
						} else { this.showVerse = false; }
					
					if(data['banner'])
						if(data['show'] == true) {
							this.banner = data; this.showBanner = true;
						} else { this.showBanner = false; }

				},
				onremotestream: (stream) => window.Janus.attachMediaStream(this.remoteVideo, stream)
			});

			/*this.intervalId = setInterval(() => this.sfuHandle.send({
				message:{ janus: 'keepalive', session_id: this.janus.getSessionId(), transaction: this.randomString(12)}
			}), 50000);*/
		}

		randomString(len) {
			let charSet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
			var randomString = '';
			for (var i = 0; i < len; i++) {
				var randomPoz = Math.floor(Math.random() * charSet.length);
				randomString += charSet.substring(randomPoz,randomPoz+1);
			}
			return randomString;
		}
	
		 streamStarted(data:any) {
			switch(data.val().started) {
				case "yes":{
					this.streamEnd = false; 
					if(this.modalEnd != null) this.modalEnd.close();
					window.Janus.init({
						debug: false, dependencies: window.Janus.useDefaultDependencies(), callback: () => {
							this.janus = new window.Janus({
								server: 'wss://07b7db0c469d.ngrok.io',
								iceServers: this.servers.iceServers,
								success: () => this.registerStream(),
								error: (cause) => console.log('CAUSE cannt create JANU ' + cause)
							})	
						}
					});		
				}
				break;
				case "no":{
					this.streamEnd = true;
					if(this.sfuHandle != null)  this.sfuHandle.send({message:{request: 'leave'}});
					if(this.textHandle != null) this.textHandle.send({message:{request: 'leave'}});
				}
				break;
				case "pause":{
                    this.player.muted(true);
					this.modalPause.open();
				}
				break;
				case "resume":{
					this.modalPause.close();
                    this.player.muted(false);
                    this._play();
				}
				break;
			}
		}
}
