
























































































































































































































































	import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	import db from "@/firebase";
	import shortid from "shortid";
	
    declare global {
		interface Window { PrismicJS: any, videojs: any, tns: any, PrismicDOM: any, kurentoUtils: any }
	}

	var touchEvent = 'onclick' in window ? 'click' : 'touchstart';

	Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class LiveStream extends Vue {
		webRTCPeer: any = null;
		ws = new WebSocket('wss://' + '54d81d39674c.ngrok.io' + '/one2many');

		prismicDOM = window.PrismicDOM; 
		private viewProgram: boolean = false;
		private remoteVideo: HTMLVideoElement;
		private askQuestionModal: boolean = false;
        private question: any = {};
		private currentMedia: any = {title:'', date:''};
		private isPlaying: boolean = false;
	
        private offerOptions: any ={ offerToReceiveVideo: 1, offerToReceiveAudio: 1 };
        private servers:any = {
            'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}, 
            {'urls': 'turn:numb.viagenie.ca','credential': 'beaver','username': 'webrtc.websitebeaver@gmail.com'}]
		};
		

		programs: any = {};
		get hasProgram() {
			return Object.keys(this.programs).length == 0;
		}

		private audioVolume: number = 0.2;
		private connections:any = {};
		private player:any = null;
		private player0: HTMLMediaElement;
		private uuid:string = shortid.generate();
		private paused:boolean = false;
		private streamEnd:boolean = true; // false
		private stream: MediaStream;

		private isProgram: boolean = true;

		//private mouseDown:number = -1; 

		page: number = 1;
		total: number = 0;
        sermons: any = [];
		first:string = null;
		currentIndex: number = 0;

		slideItems: NodeListOf<Element>;
		slideCategory:  NodeListOf<Element>;
		//categories: any = ['audio', 'video'];
		middleSection: any = [];
		newStyle: any = [];
        modalEnd: any = null;
        modalPause: any = null;
        verse: any = {};
        service_section = 'chat';
        chatList: string[] = [];
		chat: any = {name:'', content:''};
		isMuted: boolean = false;
		currentTime = '';

		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => vm._getPage());
		}

        @Watch('streamEnd') _toggleVideoModal(){
            if(this.streamEnd == true) this.modalEnd.open();
            else{this.modalEnd.close();}
        }

		public mounted() {
			this.ws.onmessage = (message) => {
				var parsedMessage = JSON.parse(message.data);
				switch (parsedMessage.id) { 
					case 'presenterResponse': this.presenterResponse(parsedMessage);break;
					case 'viewerResponse': this.viewerResponse(parsedMessage);break;
					case 'iceCandidate': this.webRTCPeer.addIceCandidate(parsedMessage.candidate); break;
					default: console.error('Unrecognized message', parsedMessage);
				}
			}
            
            db.VIDEO_VERSES.on("child_added", (data:any) => this.verse = data.val().verse);
            db.VIDEO_CHATS.on("child_added", (data:any) =>  this.chatList=[...this.chatList, JSON.parse(data.val()).chat]);
			db.VIDEO_STARTED_SIGNAL.on("value",this.streamStarted);
			this.player = window.videojs('video-id', {
				//liveui: true,
				responsive:true, controlBar: {
					pictureInPictureToggle: false,
					durationDisplay: false, timeDivider: false, liveDisplay: false,
					fullscreenToggle: false, progressControl: false
				}
			});


            var ModalDialog = window.videojs.getComponent('ModalDialog');
            this.modalEnd = new ModalDialog(this.player, {temporary: false, uncloseable: true});
            this.modalEnd.addClass('not-started-modal');
            this.player.addChild(this.modalEnd);
			
			this.modalPause = new ModalDialog(this.player, {temporary: false, uncloseable: true});
            this.modalPause.addClass('pause-modal');
            this.player.addChild(this.modalPause);
			
			//this.modalEnd.open();

			window.onbeforeunload = () => {
				var off = db.DISCONNECTED.push(JSON.stringify({uuid: this.uuid}));
				off.remove();
			}
		}

		async _getPage() {
			this._getPrograms();
		}

		async _getPrograms() {
			var response = (await _Prismic()).getSingle('program');
			response.then(d => this.programs = d.data.programs);
		}

		_sendQuestion() {
			var m = db.VIDEO_QUESTIONS.push(JSON.stringify({question:this.question}));
			m.remove();
            this.question = {};
			this.askQuestionModal = false;
		}


		beforeDestroy() {
			if(this.player) this.player.dispose();
		}

        sendChat() {
			if(this.chat.content == '') return;
			var m = db.VIDEO_CHATS.push(JSON.stringify({chat: this.chat}));
            m.remove();
            this.chat={...this.chat, content:''};
		}
		
		toggleFullScreen() {
			this.player.isFullscreen()? this.player.exitFullscreen(): this.player.requestFullscreen();
		}

		_play() {
			this.remoteVideo.play();
			this.isPlaying = true;
		}

		_pause() {
			this.remoteVideo.pause(); 
            this.isPlaying = false;
		}

		_mute() {
			this.remoteVideo.muted = true;
			this.isMuted = true;
			//this.isPlaying = true;// !this.player.paused;
		}

		_unmute() {
			this.remoteVideo.muted = false; 
			this.isMuted = false;
			//this.isPlaying = false;//this.player.paused;
		}
	
		private streamStarted(data:any) {
			switch(data.val().started) {
				case "yes":{
					this.streamEnd = false; this.modalEnd.close();
					var options = { remoteVideo: document.getElementById('video-id0'), onicecandidate : this.onIceCandidate }
					this.webRTCPeer = window.kurentoUtils.WebRtcPeer.WebRtcPeerRecvonly(options, (error) => {
						if(error) throw error;
						this.webRTCPeer.generateOffer((er, offerSDP) =>{
							this.ws.send(JSON.stringify({id : 'viewer',sdpOffer : offerSDP}));
						});
					});
								
				}
				break;
				case "no":{
					this.streamEnd = true;
				}
				break;
				case "pause":{
                    this.player.muted(true);
					this.modalPause.open();
				}
				break;
				case "resume":{
					this.modalPause.close();
                    this.player.muted(false);
                    this._play();
				}
				break;
			}
		}


	presenterResponse(message) {
		console.log('processing presenter response ..');
		this.webRTCPeer.processAnswer(message.sdpAnswer);
	}
 
	viewerResponse(message) {
		console.log('processing viewer response ..');
		this.webRTCPeer.processAnswer(message.sdpAnswer);
	}

	onIceCandidate(candidate) {
		console.log('Local candidate' + JSON.stringify(candidate));
		this.ws.send(JSON.stringify({id : 'onIceCandidate', candidate : candidate}));
	}

}
